<template>
    <div class="guide box">
        <van-swipe
            class="my-swipe"
            :autoplay="3000"
            indicator-color="white"
        >
            <van-swipe-item v-for="(img,i) in imgs" :key="i">
                <img :src="img" alt="" @click="gotoMain(i)" class="g-img">
            </van-swipe-item>
        </van-swipe>
    </div>
</template>    

<script>
import img4 from "@/assets/images/slide4.png"
export default {
    data(){
        return {
            imgs:[
                //引入本地的静态文件 必须模块化引入
                require("@/assets/images/slide1.png"),
                require("@/assets/images/slide2.png"),
                require("@/assets/images/slide3.png"),
                img4
            ]
        }
    },
    mounted(){
        //引导欢迎页 显示次数
        if(localStorage.visitCount){  //判断是否是第一次访问
            localStorage.visitCount++;
            console.log(localStorage.visitCount)
            if(localStorage.visitCount>100000){
                this.$router.push({name:"movie"})
            }
        }else{  //这是第一次访问
                localStorage.visitCount = 1;
        }
    },
    methods:{
        gotoMain(i){
            console.log(i);
            if(i==this.imgs.length-1){
                this.$router.push({name:"movie"})
            }
        }
    }
}

</script>

<style lang="scss" scoped>
.my-swipe,.g-img{
    width:100%;
    height:100%;
}
</style>